const listPets = document.querySelector(".listPets");

async function renderPets() {
  try {
    const myURL = `http://localhost:3000/petsUser`;
    console.log(myURL);
    const response = await fetch(myURL);
    const data = await response.json();

    const listMarkUp = data.map((data) => {
      console.log(data);

      const listElem = `
        <div class="photo-card">
          <div class="photo-card2">
            <div class="stats">
              <p class="stats-item">
                —ñ–º—è —Ç–≤–∞—Ä–∏–Ω–∫–∏:<br />   
                ${data.namePets}   
              </p>
              <p class="stats-item">
                –≤–ª–∞—Å–Ω–∏–∫ —Ç–≤–∞—Ä–∏–Ω–∫–∏:<br />  
                ${data.owner} 
              </p>
              <p class="stats-item">
                –æ–ø–∏—Å —Ç–≤–∞—Ä–∏–Ω–∫–∏:<br />
                ${data.director} 
              </p>
              <p class="stats-item">
                –¥–æ–º–∞—à–Ω—å–æ–º—É —É–ª—é–±–ª–µ–Ω—Ü—é:<br />
                ${2024 - data.year} —Ä–æ–∫—ñ–≤
              </p>
            </div>
          </div>
          <p class="stats-item">
            id: ${data.id}
          </p>
        </div>
      `;
      return listElem;
    });

    listPets.innerHTML = listMarkUp;
  } catch (error) {
    console.error(error);
  }
}

renderPets();

